.loader {
   width: 100px;
   height: 100px;
   border: 3px dashed red;
   border-radius: 50%;
   animation: rotate 3s infinite linear alternate;
}

@keyframes rotate {
   from {
      transform: rotate(0deg) scale(1);
   }

   to {
      transform: rotate(360deg) scale(1.4);
   }
}
